import React from "react";
import { Card, PageHeaderProps, PageHeader } from "antd";
import { useNavigate } from "react-router-dom";

export default function ResourceInfo(props: PageHeaderProps & { children?: React.ReactNode }) {
  const navigate = useNavigate();

  return (
    <Card bordered={false} bodyStyle={{padding: 0}}>
      <PageHeader {...props} ghost={true} onBack={() => navigate(-1)} />
    </Card>
  );
}
