const snippets = [
    {
        range: null,
        label: 'Dashboard: Modal',
        kind: 27,
        documentation: 'Creates a new modal.',
        insertText: 'Show-UDModal -Content {\n\tNew-UDTypography "Hello, World"\n}'
    },
    {
        range: null,
        label: 'Dashboard: Chart with Colors',
        kind: 27,
        documentation: 'Creates a chart with colors',
        insertText: `$Data = Get-Process | Sort-Object -Property CPU -Descending | Select-Object -First 10 
$Options = @{
    Type = 'bar'
    Data = $Data
    BackgroundColor = 'Red'
    BorderColor = '#c61d4a'
    HoverBackgroundColor = 'Blue'
    HoverBorderColor = '#451dc6'
    DataProperty = 'CPU'
    LabelProperty = 'ProcessName'
}

New-UDChartJS @Options`
    },
    {
        range: null,
        label: 'Dashboard: Form with validation',
        kind: 27,
        documentation: 'Creates a form with validation',
        insertText: `New-UDForm -Content {
New-UDTextbox -Id 'txtValidateForm'
} -OnValidate {
    $FormContent = $EventData

    if ($FormContent.txtValidateForm -eq $null -or $FormContent.txtValidateForm -eq '') {
        New-UDFormValidationResult -ValidationError "txtValidateForm is required"
    } else {
        New-UDFormValidationResult -Valid
    }
} -OnSubmit {
    Show-UDToast -Message $Body
}`
    },
];

export default snippets;
