import "../../Tables.module.css";
import React from "react";
import { useParams } from "react-router-dom";
import { CodeEditor, FullEditor } from "components/ui/Editor/Editor";
import { Page, PageContentBase, PageHeader } from "components/pages/Page";
import { Module } from "types";
import { Col } from "antd";
import queryClient from "components/utils/queryClient";
import { useQuery } from "react-query3";
import ResourceInfo from "components/ui/ResourceInfo";

export default function AuthenticationPage() {
    const { id } = useParams();
    const { data: module } = useQuery<Module>(
        `/module/${id}`,
        {
            enabled: !!id,
            initialData: (): Module => queryClient.getQueryData<Module>(`/module/${id}`)
        }
    );


    return (
        <Page resource={module}>
            <PageHeader>
                <ResourceInfo
                    title={module?.name}
                    subTitle={module?.version}
                ></ResourceInfo>
            </PageHeader>
            <PageContentBase>
                <Col span={24}>
                    <CodeEditor>
                        <FullEditor title="Code Editor" />
                    </CodeEditor>
                </Col>
            </PageContentBase>
        </Page>
    );
}
